export default{
  detailsButton: false,
  idField: 'ingredientId',
  path: 'ingredients',
  tableName: 'Skadniki',
  newItemTitle: 'Nowy skadnik',
  editItemTitle: 'Edytuj skadnik',
  headers: [
    {text: 'Nazwa', value: 'name', required: true},
    {text: 'Ilo', value: 'amount'},
    {text: 'Jednostka', value: 'shoppingUnit'},
    {text: 'Dostawca', value: 'supplierId', relation: true, fromTable: 'suppliers', toShow: 'name', selectField: true},
    {text: 'Typ', value: 'ingredientTypeId', relation: true, fromTable: 'ingredientTypes', toShow: 'name', selectField: true},
    {text: 'Akcje', value: 'actions'}
  ],
  polishLabels: [
    'Nazwa', 'Jednostka', 'Ilo', 'Dostawca', 'Typ', 'Akcje'
  ],
  model: {
    name: '',
    amount: '',
    shoppingUnit: '',
    supplierId: 0,
    ingredientTypeId: 0
  },
  uniqueField: 'name'
}



